let timeCounter = 0;
let min = 0;
let timeOut;
export let timeSet = 10;

const addZero = (n) => (parseInt(n, 10) < 10 ? '0' : '') + n;

export const startTimer = (callback, count, container) => {
  clearTimeout(timeOut);
  timeCounter = 0;
  const timer = () => {
    if (timeCounter === count) {
      clearTimeout(timeOut);
      callback();
      return;
    }
    timeCounter++;
    if (timeCounter % 60 === 0) {
      min++;
      timeCounter = 0;
    }
    container.innerHTML = `${addZero(min)}:${addZero(timeCounter)}`;
    timeOut = setTimeout(timer, 1000);
  };
  timer();
};

export const finishTimer = () => {
  clearTimeout(timeOut);
};

export const setTimerDuration = (value) => {
  timeSet = Number(value);
  return timeSet;
};

export const getTimerDuration = (container, text) => {
  if (timeSet > 0) {
    container.setAttribute('checked', true);
  } else {
    container.setAttribute('checked', false);
  }
  text.innerHTML = container.value;
};
