\r\n.menu-container {\r\n  height: 100vh;\r\n  padding-top: 1rem;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n}\r\n\r\n.header-menu-page {\r\n  .logo {\r\n    background: url('../assets/img/icons/logo.png');\r\n    width: 8.5rem;\r\n        height: 5.5rem;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    @media (min-width: 800px) {\r\n      width: 18rem;\r\n        height: 11.5rem;\r\n    }\r\n  }\r\n\r\n}\r\n\r\n.main-menu-page {\r\n  width: 100%;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  flex-wrap: wrap;\r\n  .menu-btn-painter-quiz {\r\n    background: url('../assets/img/icons/artists-quiz.png');\r\n  }\r\n\r\n  .menu-btn-picture-quiz {\r\n    background: url('../assets/img/icons/pictures-quiz.png');\r\n  }\r\n\r\n  .menu-btn {\r\n    cursor: pointer;\r\n    width: 19rem;\r\n    height: 13rem;\r\n    background-size: cover;\r\n    background-repeat: no-repeat;\r\n    display: flex;\r\n    justify-content: flex-end;\r\n    align-items: flex-end;\r\n    margin: 0.5rem;\r\n    filter: drop-shadow(-10px 15px 30px rgba(0, 0, 0, 0.25));\r\n    box-shadow: $btn-shadow;\r\n    transition: $standart-transition;\r\n     color: $main-color;\r\n     @media (min-width: 800px) {\r\n      width: 28rem;\r\n        height: 18rem;\r\n    }\r\n\r\n    p {\r\n      background: rgba(255, 255, 255, 0.75);\r\n      width: 100%;\r\n      font-style: normal;\r\n      font-weight: bold;\r\n      font-size: 2rem;\r\n      line-height: 44px;\r\n      text-align: right;\r\n      padding-right: 1rem;\r\n      text-transform: uppercase;\r\n     \r\n      span{\r\n        font-weight: normal;\r\n      }\r\n    }\r\n\r\n    &:hover {\r\n      color: $btn-shadow-hover;\r\n      box-shadow: $btn-shadow-hover;\r\n      transform: scale(1.01);\r\n    }\r\n  }\r\n\r\n  \r\n}\r\n.menu-btn-setting-quiz {\r\n    background-color: $main-color;\r\n    background-image: url('../assets/img/icons/setting.svg');\r\n    background-position: 0%;\r\n    background-repeat: no-repeat;\r\n    background-size: 3rem;\r\n    padding: 1rem 1rem 1rem 3.5rem;\r\n    color: $color-dark-block;\r\n    text-transform: uppercase;\r\n    transition: $standart-transition;\r\n    cursor: pointer;\r\n   &:hover{\r\n    transform: scale(1.01);\r\n    background-color: $btn-shadow-hover;\r\n   }\r\n  }\r\n.footer-menu-page {\r\n  @media (min-width: 800px) {\r\n    font-size: 2rem;\r\n  }\r\n  .footer-list {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    list-style-type: none;\r\n    opacity: 0.3;\r\n\r\n    li {\r\n      margin: 0 1rem;\r\n      color: $color-light-block;\r\n\r\n      a {\r\n        text-decoration: none;\r\n        color: $color-light-block;\r\n      }\r\n\r\n      img {\r\n        width: 2rem;\r\n        @media (min-width: 800px) {\r\n          width: 4rem;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n}","$main-color: #660033;\r\n$bgc-btn: #e0b51f;\r\n$bgc-btn-hover: #f0d470;\r\n$btn-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;\r\n$btn-shadow-hover: rgba(102, 0, 51, 0.5);\r\n$standart-transition: all 0.5s ease;\r\n$color-dark-block: #fff;\r\n$color-light-block: #000;\r\n$background-modal-wrapper: #64646477;\r\n$true-answer: #006635;\r\n$false-answer: #9b2727;","@import \"variables\";\r\n.categories-container{\r\n  height: 100%;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n\r\n.header-categories-page {\r\n  background: $color-dark-block;\r\n  position: fixed;\r\n  width: 100%;\r\n  top: 0;\r\n  left: 0;\r\n  padding-bottom: 1rem;\r\n  z-index: 100;\r\n\r\n  div {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    flex-wrap: wrap-reverse;\r\n  }\r\n\r\n\r\n  .logo {\r\n    background: url('../assets/img/icons/logo.png');\r\n    width: 5.5rem;\r\n    height: 3.5rem;\r\n    margin: 1.5rem 0;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n  }\r\n\r\n  h2 {\r\n    font-style: normal;\r\n    font-weight: normal;\r\n    font-size: 1rem;\r\n    font-weight: 900;\r\n    line-height: 29px;\r\n    text-align: center;\r\n    text-transform: uppercase;\r\n    color: $main-color;\r\n    padding: 0 1rem;\r\n    @media (min-width: 800px) {\r\n      font-size: 2rem;\r\n      padding: 0 3rem;\r\n    }\r\n\r\n  }\r\n\r\n  .categories-page-home-btn {\r\n    background-image: url('../assets/img/icons/home.svg');\r\n\r\n  }\r\n\r\n  .categories-page-score-btn {\r\n    background-image: url('../assets/img/icons/score.svg');\r\n  }\r\n\r\n  .categories-page-btn {\r\n    background-color: $main-color;\r\n    background-position: 0.2rem 50%;\r\n    background-repeat: no-repeat;\r\n    background-size: 3rem;\r\n    padding: 1rem 1rem 1rem 3.5rem;\r\n    color: $color-dark-block;\r\n    text-transform: uppercase;\r\n    transition: $standart-transition;\r\n    cursor: pointer;\r\n\r\n    &:hover {\r\n      background-color: $btn-shadow-hover;\r\n    }\r\n  }\r\n}\r\n\r\n.main-categories-page {\r\n  margin-top: 11rem;\r\n  padding-bottom: 1rem;\r\n  width: 90%;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-around;\r\n  flex-wrap: wrap;\r\n  gap: 10px 10px;\r\n  @media (min-width: 800px) {\r\n    width: 70%\r\n    \r\n  }\r\n  &.score-page{\r\n    .categories-item {\r\n      position: relative;\r\n      cursor: pointer;\r\n      display: flex;\r\n      flex-direction: column;\r\n      justify-content: center;\r\n      align-items: center;\r\n      width: 30vw;\r\n      height: calc(30vw);\r\n      color: $main-color;\r\n      font-size: 2rem;\r\n      box-shadow: $btn-shadow;\r\n      transition: width 0.8s ease;\r\n      padding: 0.1rem;\r\n  \r\n      &:hover {\r\n        color: $btn-shadow-hover;\r\n        transform: scale(1.03);\r\n      }\r\n  \r\n      &.unactive {\r\n        opacity: 0.75;\r\n        filter: grayscale(5);\r\n      }\r\n  \r\n      .num-categories {\r\n        position: absolute;\r\n        width: 3rem;\r\n        height: 3rem;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        background-color: $main-color;\r\n        top:0%;\r\n        right: 0%;\r\n        color: $color-dark-block;\r\n\r\n        .item-btn-score{\r\n          font-size: 1rem;\r\n          background-color: $main-color;\r\n          color: $color-dark-block;\r\n        }\r\n        .number-item {\r\n          font-weight: bold;\r\n          line-height: 44px;\r\n        }\r\n  \r\n        .question-on-item {\r\n      display: none;\r\n        }\r\n      }\r\n  \r\n      .categories-item-bg {\r\n        width: 100%;\r\n        height: 100%;\r\n        background-repeat: no-repeat;\r\n        background-position: top center;\r\n        background-size: cover;\r\n      }\r\n    }\r\n  }\r\n  .categories-item {\r\n    cursor: pointer;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    width: 30%;\r\n    color: $main-color;\r\n    font-size: 3rem;\r\n    box-shadow: $btn-shadow;\r\n    transition: width 0.8s ease;\r\n    padding: 0.1rem;\r\n    @media (min-width: 800px) {\r\n      width: 20%;\r\n      \r\n    }\r\n\r\n    &:hover {\r\n      color: $btn-shadow-hover;\r\n      transform: scale(1.03);\r\n    }\r\n\r\n    &.unactive {\r\n      opacity: 0.75;\r\n      filter: grayscale(5);\r\n    }\r\n\r\n    .num-categories {\r\n      width: 100%;\r\n      display: flex;\r\n      justify-content: space-between;\r\n      .item-btn-score{\r\n        width: calc(50%);\r\n        background-size: 40%;\r\n        background-repeat: no-repeat;\r\n        background-position: center;\r\n        background-color: $main-color;\r\n        background-position: left;\r\n        color: $color-dark-block;\r\n        display: flex;\r\n    justify-content: center;\r\